
.video-list {
    display: flex;
    justify-items: center;
    flex-direction: column;
    padding: 0 5vw 0 5vw;
}

.video-block {
    display: flex;
    padding: 0 3.5vw 3vh 5vw;
    flex-direction: column;
    background-color: #2E2E2E;
    border-radius: 5px;
    margin-bottom: 3.5vh;
}